<!--

-->
<template>
  <el-form inline class="demo-form-inline">
    <el-form-item label="" class="whiteItem" >
      <el-input placeholder="" v-model="spdNo"></el-input>
    </el-form-item>
    <el-form-item label="" class="whiteItem">
      <el-select v-model="versionId" clearable placeholder="">
        <el-option
          v-for="item in options"
          :key="item.versionId"
          :label="item.label"
          :value="item.versionId">
        </el-option>
      </el-select>
    </el-form-item>
    <el-form-item label="" class="whiteItem">
      <el-checkbox-group v-model="elestatus">
        <el-checkbox-button v-for="city in cities" :label="city.key" :key="city.key">{{city.value}}</el-checkbox-button>
      </el-checkbox-group>
    </el-form-item>
    <el-form-item label="" class="whiteItem">
      <el-cascader
        v-model="areaCode"
        :options="areaList"
        :props="{ checkStrictly: true,value:'code',label:'name',children:'areaChildren',expandTrigger: 'hover' }"
        clearable></el-cascader>
    </el-form-item>
    <el-form-item>
      <el-button type="primary" icon="el-icon-search" @click="querySubmit()"></el-button>
    </el-form-item>
  </el-form>
</template>

<script>
  import {getNotDataRequest} from '../../utils/api'
  import {postJsonRequest} from '../../utils/api'
  const cityOptions = [{
    key: 0,
    value: ''
  }, {
    key: 1,
    value: ''
  }, {
    key: 2,
    value: ''
  }];
  export default {
    name: 'devInfo',
    props: ['message', 'value'],
    methods:{
      // 
      querySubmit () {
        // 
        this.$emit('loadOn', true);
        // 
        this.urlparams.likeMap.spdNo = this.spdNo
        // 
        this.urlparams.eqMap.versionId = this.versionId
        // null
        if (this.areaCode) {
          this.urlparams.likeMap.areaCode = this.areaCode[this.areaCode.length - 1]
        } else {
          this.urlparams.likeMap.areaCode = null
        }
        // 
        this.urlparams.status = this.elestatus
        this.selEquipment()
      },
      // 
      selEquipment(){
        // 
        postJsonRequest('/spd/equipmentList', this.urlparams).then(resp => {
          if (resp.status == 200) {
            // 
            this.$emit('changeMsg', resp.data.content);
            // 
            this.$emit('loadOn', false);
          }
          // TODO 
          else {

          }
        })
      },
      // 
      getValue(){
        // 
        getNotDataRequest('/area/areaList').then(resp => {
          if (resp.status == 200) {
            this.areaList = resp.data
            console.log(this.areaList)
            console.log('xxxxx',this.content)
          }
          // TODO 
          else {

          }
        })
      }
    },
    mounted: function () {
      this.getValue()
    },
    data(){
      return{
        // 
        content:this.message,
        loading:this.value,
        // 
        spdNo: '',
        // 
        versionId: null,
        // 
        options: [{
          versionId: 1,
          label: 'IES-1/160'
        }, {
          versionId: 2,
          label: 'IES-4/80'
        }, {
          versionId: 3,
          label: 'IES-4/40'
        }],
        // 
        elestatus: [],
        // 
        areaList:[],
        // 
        areaCode: null,
        // 
        cities:cityOptions,
        //
        urlparams: {
          eqMap: {},
          likeMap: {},
          status: [],
          pageNum: 1,
          pageSize: 10
        }
      }
    }
  }
</script>

<style>
  @import '../home/dataBox/index.less';
</style>



